const functions = [
  {
    name: 'open',
    params: [],
    details: `The event is triggered each time the dropdown opens. `
  },
  {
    name: 'close',
    params: [],
    details: `The event is triggered each time the dropdown closes.`
  },
  {
    name: 'align',
    params: [
      { key: 'type', value: 'string' },
      { key: 'value', value: 'left | right | top | bottom' }
    ],
    details: `Returns the position of the dropdown relative to the join element after opening. The values that can be sent are: left, right, top, bottom.`
  },
  {
    name: 'arrow-align',
    params: [
      { key: 'type', value: 'string' },
      { key: 'value', value: 'left | right, top, bottom' }
    ],
    details: `Returns the position of the arrow relative to the dropdown join after opening. The values that can be sent are: left, right, top, bottom.`
  },
  {
    name: 'arrow-on',
    params: [
      { key: 'type', value: 'string' },
      { key: 'value', value: '[left, right, top, bottom, middle]' }
    ],
    details: `Returns the position of the arrow relative to the dropdown join after opening. The values that can be sent are: left, right, top, bottom, middle.`
  },
  {
    name: 'justify',
    params: [{ key: 'type', value: 'boolean' }],
    details: `Returns true if the dropdown is positioned in the middle of the join element, and false otherwise.`
  },
  {
    name: 'esc-keydown',
    params: [],
    details: `Triggers when the escape button is pressed while the dropdown is open. 
      `
  },
  {
    name: 'other-area-clicked',
    params: [],
    details: `Fires when a zone other than the dropdown receives a click while the dropdown is open.`
  }
];

let readme = '';

functions.forEach(f => {
  readme += `## ${f.name}
  `;

  f.params.forEach(param => {
    readme += `**${param.key}**: _${param.value}_
    `;
  });

  readme += `${f.details}
  `;
});

export default functions;
